.App {
  display: -ms-flexbox;
  display: flex;
}

.Map {
  width: 80%;
}

.Filter {
  z-index: 10;
  position: absolute;
  top: 0;
  right: 0;
  width: 33%;
}

.text-filter {
  width: 100%;
}

.List {
  width: 20%;
  height: 600px;
  overflow-y: scroll;
  display: -ms-flexbox;
  display: flex;
  overflow-x: hidden;
}

.tombs-list ul {
  list-style: none;
  padding-left: 0;
}

.list-button {
  width: 100%;
}

.tombs-list ul li::before {
  content: '\200B';
}

.info-thumbnail {
  height: 200px;
}
.info-with-img {
  display: -ms-flexbox;
  display: flex;
}
.info-text {
  font-size: 12px;
}

.checkboxes {
  background-color: white;
}

.checkbox-text {
  font-size: 12px;
}



